@import 'fonts';

.kima-card {
  position: relative;

  .invalid-option-banner {
    font-size: 14px;
    background-color: red;
    color: $financeLighter;
    padding: 10px 25px;
    margin: 0;
    border-radius: 40px 40px 0 0;
    text-align: justify;
  }

  .kima-card-header {
    display: flex;
    flex-direction: column;
    padding: 20px 16px;
    border-bottom: 2px solid $dividerPrimary;

    @include breakpoint(phablet) {
      padding: 30px 10px;
    }

    .topbar {
      display: flex;
      align-items: center;
      justify-content: space-between;

      @include breakpoint(phablet) {
        flex-direction: column-reverse;
        align-items: flex-start;
      }

      .title {
        display: flex;
        align-items: center;
        font-family: 'Sohne';
        font-weight: 400;
        letter-spacing: 1px;
        font-size: 26px;

        @include breakpoint(phablet) {
          font-size: 24px;
        }

        &.skeleton {
          background: $borderSecondary;
          height: 25px;
          width: 60%;
          @include pulse-loader();
        }

        .transaction-title {
          margin-right: 10px;
          font-size: 26px;
          text-align: start;

          @include breakpoint(phablet) {
            font-size: 20px;
          }
        }

        span {
          color: $textSecondary;
          font-weight: bold;
          margin: 0 0 0 1em;
        }

        .title-icon {
          display: inline-block;
          vertical-align: middle;
        }

        .percent {
          margin-left: 0.5em;
          font-weight: bold;
        }

        // @include breakpoint(phablet) {
        //   flex-direction: column;
        //   align-items: flex-start;

        //   p {
        //     margin: 0;
        //   }
        // }
      }

      .control-buttons {
        display: flex;
        align-items: center;

        @include breakpoint(phablet) {
          margin-left: auto;
        }

        .icon-button {
          background: transparent;
          border: 1px solid $financeGrey;
          border-radius: 40px;
          padding: 2px 8px;
          cursor: pointer;
          @include transition;

          &.minimize {
            border: none;
          }

          &:nth-of-type(2) {
            margin-left: 13px;
            padding: 4px 6px;
          }

          &:hover,
          &:disabled {
            opacity: 0.6;
          }

          &:disabled {
            cursor: default;
          }

          svg {
            width: 30px;
            height: 22px;
            padding: 5px;

            @include breakpoint(phablet) {
              width: 25px;
            }
          }

          &:first-of-type svg {
            margin-top: 7px;
          }
        }

        .cross-icon-button {
          display: flex;
          justify-content: center;
          align-items: center;
          padding: 4px;
          width: 60px;
          height: 60px;
          border-radius: 9999px;
          background: transparent;
          cursor: pointer;
          border: 1px solid $financeGrey;

          @include breakpoint(phablet) {
            width: 50px;
            height: 50px;
          }

          svg {
            width: 24px;
            height: 24px;

            @include breakpoint(phablet) {
              width: 20px;
              height: 20px;
            }
          }

          &:hover,
          &:disabled {
            opacity: 0.6;
          }

          &:disabled {
            cursor: default;
          }
        }

        a {
          margin-left: auto;
          color: $finance;
          text-decoration: none;
          cursor: pointer;
          margin-right: 1em;
        }

        .menu-button {
          font-family: 'Violet';
          font-size: 14px;
          font-weight: 500;
          padding: 4px 10px 4px 10px;
          border: 1px solid $dividerPrimary;
          border-radius: 50px;
          display: flex;
          gap: 10px;
          background: $backgroundTertiary;
          color: var(--text-80, rgba(255, 255, 255, 0.8));

          &:hover {
            opacity: 0.7;
          }

          @include breakpoint(mobile) {
            font-size: $fontSmall;
          }
        }

        .reset-button {
          font-family: 'Violet';
          font-size: 14px;
          font-weight: 500;
          padding: 4px 16px;
          border: 1px solid $dividerPrimary;
          border-radius: 50px;
          background-color: $backgroundTertiary;
          color: var(--text-80, rgba(255, 255, 255, 0.8));
          cursor: pointer;

          &:hover {
            opacity: 75%;
          }

          @include breakpoint(mobile) {
            font-size: $fontSmall;
          }
        }

        .maximize {
          color: white;
          cursor: pointer;
        }
      }
    }

    .header-network-labels {
      display: flex;
      flex-direction: row;
      align-items: center;
      gap: 16px;
      margin-top: 20px;

      p {
        padding: 0;
      }

      @include breakpoint('max-width: 390px') {
        p {
          display: none;
        }

        .arrow {
          transform: rotate(-180deg);
        }
      }

      .arrow {
        transform: rotate(-90deg);
      }
    }

    h4 {
      font-weight: 300;
      font-size: 14px;
      margin-top: 20px;
      margin-bottom: 30px;
    }

    .kima-card-network-label {
      display: flex;
      padding: 13px 18px;
      // width: 190px;
      border-radius: 50px;
      margin-right: 12px;

      .icon {
        border-radius: 9999px;
        background-color: white;
        padding: 4px;
        display: flex;
        justify-content: center;
      }

      @include breakpoint(mobile) {
        font-size: 14px;
      }

      &.light {
        background-color: $hover;
        color: black;
      }

      &.dark {
        background-color: $financeDark;
        color: white;
      }
    }

    p {
      margin: 0;
      border-radius: 50px;
      padding: 1em 1.5em;

      &.light {
        background-color: $financeLighter;
        color: black;
      }

      &.dark {
        background-color: $finance;
        color: white;
      }
    }
  }

  .kima-card-footer {
    display: flex;
    flex-direction: row;
    align-items: center;
    @include transition;
    max-height: 50px;
    margin-top: 20px;
    opacity: 1;
    box-sizing: content-box;
    font-family: 'Violet';
    font-size: 14px;
    font-weight: 700;
    padding: 0 20px;

    @include breakpoint('min-width: 768px') {
      margin-bottom: 20px;
    }

    @include breakpoint(phablet) {
      padding-top: 20px;

      p {
        display: none;
      }

      &.confirm {
        margin-top: 50px;

        @include breakpoint(mobile) {
          margin-top: 0;
        }
      }

      &.transaction {
        margin-top: 0;
      }
    }

    @include breakpoint(mobile) {
      padding: 0;
      width: 100%;
      flex-direction: column-reverse;
    }

    .kima-powered {
      display: flex;
      flex-direction: column;
      color: #666666;
      font-family: 'Sohne';
      font-size: 10px;

      @include breakpoint(mobile) {
        flex-direction: row;
        gap: 6px;
        justify-content: center;
        align-items: center;
      }

      .kima-logo {
        align-items: center;
        display: flex;
        flex-direction: row;
        gap: 6px;
        font-size: 16px;
        font-weight: 800;
      }
    }

    .button-group {
      display: flex;
      align-items: center;
      box-sizing: content-box;
      margin-left: auto;
      flex-direction: row;
      gap: 20px;
      margin-bottom: 30px;
      padding: 20px;

      &.skeleton {
        align-items: flex-end;
      }

      .skeleton {
        background: $borderSecondary;
        height: 30px;
        width: 100px;
        @include pulse-loader();
      }

      @include breakpoint(mobile) {
        margin-top: 0px;
        flex-direction: column;
        width: 100%;
      }

      button {
        padding: 8px;
        min-width: 80px;
        height: 40px;

        @include breakpoint(mobile) {
          width: 80%;
        }
      }

      .secondary-button {
        color: white;
        display: flex;
        gap: 10px;
        justify-content: center;
        align-items: center;
        font-family: 'Roboto Mono', monospace;

        &.hex-button {
          color: white;
          position: relative;
          background: transparent;
          padding: 4px 16px;
          font-weight: bold;
          border: none;
          cursor: pointer;
          z-index: 1;

          clip-path: polygon(
            10px 0%,
            /* Top-left horizontal */ 100% 0%,
            /* Top-right */ 100% calc(100% - 10px),
            /* Bottom-right start */ calc(100% - 10px) 100%,
            /* Bottom-right hex corner */ 0% 100%,
            /* Bottom-left */ 0% 10px /* Top-left hex corner */
          );
          overflow: hidden;
        }

        &.hex-button::before {
          content: '';
          position: absolute;
          inset: 0;
          z-index: -2;
          background: var(--text-60, rgba(255, 255, 255, 0.6));

          clip-path: polygon(
            10px 0%,
            /* Top-left start */ 100% 0%,
            /* Top-right */ 100% calc(100% - 10px),
            /* Bottom-right start */ calc(100% - 10px) 100%,
            /* Bottom-right hex corner */ 0% 100%,
            /* Bottom-left */ 0% 28px,
            /* Left border visible portion */ 4px 24px,
            /* Start of cut */ 0% 20px,
            /* End of cut */ 0% 10px /* Continue up to top-left hex corner */
          );
        }

        &.hex-button::after {
          content: '';
          position: absolute;
          inset: 1.5px;
          z-index: -1;
          background: #121212;
          clip-path: inherit;
        }

        &:hover,
        &:disabled {
          opacity: 0.7;
        }

        &:disabled {
          cursor: default;
        }

        @include breakpoint(mobile) {
          &:hover {
            opacity: 1;
          }

          &:hover:focus:active {
            opacity: 0.7;
          }
        }
      }

      .hex-button {
        color: white;
        position: relative;
        background: transparent;
        padding: 4px 16px;
        font-weight: bold;
        border: none;
        cursor: pointer;
        z-index: 1;
        display: flex;
        align-items: center;
        justify-content: center;

        clip-path: polygon(
          10px 0%,
          /* Top-left horizontal */ 100% 0%,
          /* Top-right */ 100% calc(100% - 10px),
          /* Bottom-right start */ calc(100% - 10px) 100%,
          /* Bottom-right hex corner */ 0% 100%,
          /* Bottom-left */ 0% 10px /* Top-left hex corner */
        );
        overflow: hidden;
      }

      .hex-button::before {
        content: '';
        position: absolute;
        inset: 0;
        z-index: -2;
        background: linear-gradient(135deg, #794bca, #c34c52, #e69597);

        clip-path: polygon(
          10px 0%,
          /* Top-left start */ 100% 0%,
          /* Top-right */ 100% calc(100% - 10px),
          /* Bottom-right start */ calc(100% - 10px) 100%,
          /* Bottom-right hex corner */ 0% 100%,
          /* Bottom-left */ 0% 28px,
          /* Left border visible portion */ 4px 24px,
          /* Start of cut */ 0% 20px,
          /* End of cut */ 0% 10px /* Continue up to top-left hex corner */
        );
      }

      .hex-button::after {
        content: '';
        position: absolute;
        inset: 1.5px;
        z-index: -1;
        background: #121212;
        clip-path: inherit;
      }
    }

    p {
      font-size: 0.8em;
      margin: 0 0 0 1em;
    }

    @include breakpoint(mobile) {
      max-height: unset;

      &.bridge {
        margin-top: 0;
      }

      .button-group {
        margin-left: unset;
      }
    }
  }

  .floating-footer {
    color: black;
    position: absolute;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: none;
    font-family: 'Violet';
    right: 0;
    margin-top: 10px;
    height: 35px;
    

    @include breakpoint(phablet) {
      margin-top: 35px;

      &.status {
        margin-top: 30px;
      }
    }

    @include breakpoint(mobile) {
      margin-top: 30px;
    }

    .items {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      text-decoration: none;
      padding: 4px 15px;
      border-radius: 20px;
      font-size: 12px;
      gap: 8px;
      height: 35px;

      &.light {
        background-color: $hover;
      }

      &.dark {
        background-color: $finance;
      }

      span {
        margin-right: 8px;
        font-weight: 400;
      }

      strong {
        margin-left: 5px;
        font-size: 14px;
        font-family: 'Violet';
        font-weight: bolder;
      }
    }
  }
}
